<template>
  <div class="share">
    <van-nav-bar
    fixed
    title="分享下载链接"
    left-arrow
    @click-left="onClickLeft">
    </van-nav-bar>
    <div class="share-line">
      <div>
        <div class="share-item-icon" @click="shareToChat">
          <van-icon class="iconfont share-item-icon-size" class-prefix='icon' name='wechat1'></van-icon>
        </div>
        <p class="share-item-txt">微信好友</p>
      </div>
      <div>
        <div class="share-item-icon" @click="shareToChatHub">
          <van-icon class="iconfont share-item-icon-size" class-prefix='icon' name='pengyouquan'></van-icon>
        </div>
        <p class="share-item-txt">朋友圈</p>
      </div>
      <div>
        <div class="share-item-icon" style="background-color: #68AEFB;" @click="shareToMsg">
          <van-icon class="iconfont share-item-icon-size" class-prefix='icon' name='message'></van-icon>
        </div>
        <p class="share-item-txt">短信</p>
      </div>
    </div>
    <div class="share-code">
      <span class="share-code-top">Android</span>
      <span class="share-code-bottom">iOS同上</span>
    </div>
  </div>
</template>

<script>
import { Icon, NavBar } from 'vant'

export default {
  components: {
    [Icon.name]: Icon,
    [NavBar.name]: NavBar
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/me/about',
        query: {}
      })
    },
    shareToChat () {
      console.log('object')
    },
    shareToChatHub () {
      console.log('object')
    },
    shareToMsg () {
      console.log('object')
    }
  }
}
</script>

<style scoped>
.share{
  text-align: center;
  font-size: 13px;
  color: #666;
}
.share-line{
  display: flex;
  justify-content: space-around;
  margin: 100px 0 40px 0;
}
.share-item-icon{
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #4CCC7B;
  color: #fff;
  line-height: 45px;
}

.share-item-icon-size{
  font-size: 30px;
}
.share-code{
  width: 230px;
  height: 230px;
  margin: 40px auto;
  border: 1px solid #fafafa;
  position: relative;
}
.share-code-top{
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
}
.share-code-bottom{
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%)
}
</style>



